/* =============================
   1. GLOBAL STYLES 
================================ */
html,
body {
  height: 100%;
  width: 100%;
}
body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  background: #fff;
  line-height: normal;
}
.wrapper {
  position: relative;
  width: 100%;
  overflow: hidden;
}
a {
  -webkit-transition: all 0.35s;
  -moz-transition: all 0.35s;
  transition: all 0.35s;
  color: #1d1e20;
  text-decoration: none;
}
button {
  -webkit-transition: all 0.35s;
  -moz-transition: all 0.35s;
  transition: all 0.35s;
}
a:hover {
  color: #abd46e;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Roboto", sans-serif;
  margin: 0;
  color: #1d1e20;
  font-weight: 500;
}
p {
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  line-height: 21px;
  margin-bottom: 0;
  color: #666666;
  font-weight: 400;
}
/* =============================
   2. COMMON STYLES 
================================ */
.no-padding {
  padding: 0 !important;
}
.overlay {
  background-image: -webkit-linear-gradient(bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0) 100%);
  background-image: -moz-linear-gradient(bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0) 100%);
  background-image: -o-linear-gradient(bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0) 100%);
  background-image: -ms-linear-gradient(bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0) 100%);
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0) 100%);
  width: 100%;
  height: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
}
.overlay-alt {
  background: #181818;
  opacity: 0.2;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
}
.container {
  margin: 0 auto;
  padding: 0;
}
/* =============================
   3. LAYOUT STYLES 
================================ */
.layout_1 .row {
  margin: 0 -0.5px;
}
.layout_1 [class*="col-"] {
  padding: 0 0.5px;
}
.layout_1--item {
  margin-bottom: 1px;
  position: relative;
  overflow: hidden;
}
.layout_1--item img {
  transform: scale(1);
  transition: .3s;
  -webkit-transition: .3s;
  -moz-transition: .3s;
}
.layout_1--item:hover img {
  transform: scale(1.1);
  transition: .3s;
  -webkit-transition: .3s;
  -moz-transition: .3s;
}
.badge {
  font-size: 10px;
  line-height: 17px;
  border-radius: 0;
  padding: 0 5px;
  display: table;
  font-weight: bold;
  letter-spacing: 0.12em;
}
.badge-overlay {
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 11;
}
.badge-tech {
  background-color: #f65c2d;
}
.badge-travel {
  background-color: #26c0b5;
}
.badge-science {
  background-color: #8e3ec7;
}
.badge-lifestyle {
  background-color: #86c62a;
}
.badge-world {
  background-color: #26c1e5;
}
.badge-entertainment {
  background-color: #f23870;
}
.badge-health {
  background-color: #85ba41;
}
.badge-sports {
  background-color: #eb3a22;
}
.badge-politics {
  background-color: #4a77bc;
}
.badge-culture {
  background-color: #d02bbe;
}
.layout-detail {
  position: absolute;
  bottom: 0;
  width: 100%;
  left: 0;
  z-index: 11;
}
.layout-detail h4 {
  font-size: 32px;
  line-height: 36px;
  color: #fff;
  margin: 9px 0;
}
.layout-detail h5 {
