/* =============================
   4. VIDEO STYLES 
================================ */
.video-carousel-wrap {
  padding: 40px 0 60px;
}
.video-carousel-wrap h5 {
  background: #2d2e30;
  height: 10px;
  margin: 0 0 23px;
}
.video-carousel-wrap h5 span {
  color: #fff;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 14px;
  background: #1d1e20;
  padding-right: 14px;
  letter-spacing: 0.06em;
  position: relative;
  top: -2px;
}
.video-carousel-wrap h5 a {
  color: #777777;
  text-transform: uppercase;
  font-size: 10px;
  line-height: 10px;
  background: #1d1e20;
  padding-left: 14px;
  letter-spacing: 0.10em;
  position: relative;
  top: -1px;
  float: right;
}
.video-carousel-wrap h5 a:hover {
  color: #abd46e;
}
.video-container {
  position: relative;
}
.video-container img {
  width: 100%;
}
.video-container video {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  z-index: 777;
  width: 100%;
  background: #000;
  opacity: 0;
  visibility: hidden;
  transition: .3s;
  -webkit-transition: .3s;
  -moz-transition: .3s;
}
.video-container video.active {
  opacity: 1;
  visibility: visible;
  transition: .3s;
  -webkit-transition: .3s;
  -moz-transition: .3s;
}
.video-container .layout-detail {
  opacity: 1;
  transition: .3s;
  -webkit-transition: .3s;
  -moz-transition: .3s;
}
.video-container .layout-detail.hide {
  opacity: 0;
  transition: .3s;
  -webkit-transition: .3s;
  -moz-transition: .3s;
}
.video-container .layout_1--item img,
.video-container .layout_1--item:hover img {
  transform: scale(1);
}
.video-play {
  height: 74px;
  width: 74px;
  background: url(/images/icon/play.png) no-repeat center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
  z-index: 22;
  cursor: pointer;
  transition: .3s;
  -webkit-transition: .3s;
  -moz-transition: .3s;
}
.video-play:hover {
  transform: translate(-50%, -50%) scale(1.1);
  transition: .3s;
  -webkit-transition: .3s;
  -moz-transition: .3s;
}
.video-play2 {
  height: 74px;
  width: 74px;
  background: url(/images/icon/play.png) no-repeat center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
  z-index: 22;
  cursor: pointer;
  transition: .3s;
  -webkit-transition: .3s;
  -moz-transition: .3s;
}
.video-play2:hover {
  transform: translate(-50%, -50%) scale(1.1);
  transition: .3s;
  -webkit-transition: .3s;
  -moz-transition: .3s;
}
.video-play-32 {
  height: 32px;
  width: 32px;
  background: url(/images/icon/video.png) no-repeat center;
  position: absolute;
  top: 50%;
  left: 50%;
