  z-index: 22;
  cursor: pointer;
  transition: .3s;
  -webkit-transition: .3s;
  -moz-transition: .3s;
}
.now-playing {
  padding: 0 20px 0 50px;
  background: #85ba41;
  border-bottom: 2px solid #719e37;
  position: relative;
}
.now-playing p {
  color: #fff;
  padding: 8px 0 6px;
  font-size: 15px;
  transition: .4s;
  -webkit-transition: .4s;
  -moz-transition: .4s;
}
.now-playing p span {
  font-size: 11px;
