}
.video-list {
  background: #1d1e20;
  position: relative;
  overflow: hidden;
}
.video-list:after {
  content: "";
  background-image: -webkit-linear-gradient(bottom, #1d1e20, rgba(29, 30, 32, 0));
  background-image: -moz-linear-gradient(bottom, #1d1e20, rgba(29, 30, 32, 0));
  background-image: -o-linear-gradient(bottom, #1d1e20, rgba(29, 30, 32, 0));
  background-image: -ms-linear-gradient(bottom, #1d1e20, rgba(29, 30, 32, 0));
  background-image: linear-gradient(to top, #1d1e20, rgba(29, 30, 32, 0));
  width: 100%;
  height: 55px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 7777;
}
.video-list ul {
  padding: 0;
  margin: 0;
  position: relative;
  overflow: scroll;
  overflow-x: hidden;
}
.video-list ul li {
  list-style: none;
  padding: 0 20px 0 135px;
  margin: 0;
  position: relative;
  min-height: 102px;
  border-bottom: 1px solid #2f3031;
  cursor: pointer;
}
.video-list ul li:hover {
  background: #2d2e30;
