/* =============================
   5. PAGINATION
================================ */
.pagination {
  border-radius: 0;
  margin: 0 0 -6px;
}
.pagination li {
  margin-right: 2px;
  float: left;
}
.pagination li a,
.pagination li a:focus {
  width: 30px;
  height: 30px;
  background: transparent;
  color: #666666;
  border-radius: 3px;
  padding: 0;
  line-height: 28px;
  text-align: center;
  border: 1px solid transparent;
}
.pagination li.active a:hover,
.pagination li.active a,
.pagination li.active a:focus {
  width: 30px;
  height: 30px;
  background: #85ba41;
  border: none;
  color: #fff;
  border-radius: 3px;
  padding: 0;
  line-height: 30px;
  text-align: center;
}
.pagination li a:hover {
  background: transparent;
  border: 1px solid #85ba41;
  color: #85ba41;
}
.pagination li span {
  width: 30px;
  height: 30px;
  background: transparent !important;
  border: none;
  color: #666666;
  border-radius: 3px;
  padding: 0;
  line-height: 30px;
  text-align: center;
}
