  padding: 10px 0 0;
  height: initial;
}
.header1 .offset-trigger {
  width: 20px;
  height: 16px;
  float: left;
  margin-right: 20px;
  margin-top: 37px;
  cursor: pointer;
}
.header1 .offset-trigger span {
  width: 100%;
  height: 2px;
  background: #111111;
  display: block;
  margin-bottom: 5px;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.header1 .offset-trigger:hover span {
  background: #aaa;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.header1 .offset-trigger.active span:first-child {
  transform: rotate(45deg);
  position: relative;
  top: 7px;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.header1 .offset-trigger.active span:nth-child(2) {
  opacity: 0;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.header1 .offset-trigger.active span:last-child {
  transform: rotate(-45deg);
  position: relative;
  top: -7px;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
