.magazine-container .magazine-section {
  display: table;
  padding: 0;
  background-color: #ededed;
  width: 100%;
  margin-bottom: 24px;
}
.magazine-container .magazine-section .magazine-image-details {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  float: left;
  width: 100%;
}
.magazine-container .magazine-section .magazine-image-details .image {
  width: 600px;
  float: left;
  padding: 25px;
}
.magazine-container .magazine-section .magazine-image-details .image img.fill {
  width: 100%;
}
.magazine-container .magazine-section .magazine-image-details .image .exp-button {
  text-align: center;
}
.magazine-container .magazine-section .magazine-image-details .exp-button a {
  color: #000;
  font-size: 20px;
  padding: 10px 30px;
  text-align: center;
  border: 1px solid #000;
  display: inline-block;
  background-color: #FFF;
  margin-top: 10px;
}
.magazine-container .magazine-section .magazine-image-details .details {
  width: 60%;
  padding: 25px;
  font-size: 1.3rem;
}
.magazine-container .magazine-section .magazine-image-details .details h3 {
  color: #4AC0B9;
  padding: 14px 0 3px;
}
.magazine-container .magazine-section .magazine-image-details .details ul {
  list-style: none;
  color: #4AC0B9;
  margin-top: inherit;
  padding-left: 0;
}
.magazine-container .magazine-section .magazine-image-details .details ul li.icon.bullet-arrow {
  display: block;
  list-style: none;
  text-align: left;
  line-height: 1.7em;
  font-size: 14px;
  padding-left: 22px;
}
.magazine-container .magazine-section .magazine-image-details .details ul li:before {
  font-size: 16px;
  position: absolute;
  margin-left: -25px;
  content: '\f138';
  font-family: 'FontAwesome', sans-serif;
}
.magazine-container .magazine-section .magazine-image-details .details ul.precios li.icon.bullet-arrow {
  font-size: 18px;
}
.magazine-container .magazine-section .magazine-image-details .details p {
  font-size: 12px;
}
.magazine-container .magazine-section .magazine-image-details .suscribe a {
  background-color: #fff;
  color: #000;
  border: 1px solid #000;
  font-family: 'Martel', sans-serif;
  padding: 6px 20px;
  text-align: center;
  display: inline-block;
  cursor: pointer;
  font-size: 1rem;
}
.magazine-container .magazine-section .magazine-image-details .suscribe a :hover {
  background-color: #4AC0B9;
  color: #FFF;
  border: 1px solid #fff;
}
.magazine-container .magazine-section .magazine-image-details .magazine-others {
  width: 100%;
  overflow: hidden;
}
.magazine-container .magazine-section .magazine-image-details .magazine-others .magazine {
  float: left;
  padding: 25px 0;
  width: 33.33333333%;
}
.magazine-container .magazine-section .magazine-image-details .magazine-others .magazine .image {
  width: 100%;
  float: left;
  padding: 10px;
}
.magazine-container .magazine-section .magazine-image-details .magazine-others .magazine .image img.fill {
  width: 100%;
}
.magazine-container .magazine-section .magazine-image-details .magazine-others .clear {
  clear: both;
}
.item-edicion-anterior {
  border-right: 1px dotted #000;
  margin-top: 20px;
}
.item-edicion-anterior:nth-child(1),
.item-edicion-anterior:nth-child(2) {
  margin-top: 0;
}
.item-edicion-anterior .fecha {
  text-align: center;
  margin: 15px 0 0 0;
  border: 1px dotted #000;
  border-left: none;
  border-right: none;
  padding: 5px 0;
}
.item-edicion-anterior .fecha i {
  color: #c10b19;
  margin-right: 5px;
}
@media screen and (max-width: 767px) {
  .magazine-container h1 {
    padding: 10px 25px;
  }
  .magazine-container .magazine-section {
    padding: 0;
  }
  .magazine-container .magazine-section .magazine-image-details {
    display: block;
    float: left;
    width: 100%;
  }
  .magazine-container .magazine-section .magazine-image-details .details {
    width: 100%;
  }
  .magazine-container .magazine-section .magazine-image-details .details ul li.icon.bullet-arrow {
    font-size: .7em;
  }
  .magazine-container .magazine-section .magazine-image-details h2 {
    text-align: center;
  }
  .magazine-container .magazine-section .magazine-image-details .image {
    display: block;
    float: inherit;
    height: auto;
    margin: auto;
    width: 100%;
    padding: 25px;
  }
  .magazine-container .magazine-section .magazine-image-details .image img.fill {
    width: 100%;
  }
  .magazine-container .magazine-section .magazine-image-details .suscribe {
    display: block;
    margin: auto;
    padding: 12px;
    width: 70%;
    text-align: center;
  }
  .magazine-container .magazine-section .magazine-others {
    width: 100%;
  }
  .magazine-container .magazine-section .magazine-others .magazine {
    display: none;
  }
}
